<template>
        <v-row dense>
            <app-portfolio-loading v-if="loading && portfolios.length > 0"></app-portfolio-loading>
            <v-col v-else cols="6" xs="6" sm="4" md="4" lg="4"
                   v-for="(port, index) in portfolios" :key="index">
                <v-card
                        outlined
                        class="v-card--hover"
                >
                    <div v-if="port.status === 'wait for approval'" class="main-bg-color text-center">
                        <span class="white--text">
                            ລໍຖ້າອະນຸມັດ
                        </span>
                    </div>
                    <div v-else-if="port.status === 'edit'" class="main-bg-color text-center">
                        <span class="white--text">
                            ແກ້ໄຂຂໍ້ມູນຄືນໃໝ່
                        </span>
                    </div>
                        <v-img
                                :src="$server + port.images[0]"
                                :lazy-src="$server + port.images[0]"
                                :height="$vuetify.breakpoint.mdAndUp ? 190 : 150"
                                class="portfolio-img"
                        >
                            <template v-slot:placeholder>
                                <v-row
                                        class="fill-height"
                                        align="center"
                                        justify="center"
                                >
                                    <v-progress-circular size="60" indeterminate
                                                         color="primary"
                                    ></v-progress-circular>
                                </v-row>
                            </template>
                        </v-img>
                    <small class="ma-2">{{ port.title }}</small>
                    <v-card-actions>
                        <v-rating
                                :value="
                                     ((port.reviews.map(i=>i.rating)).reduce((a, b) => a + b, 0)) / port.reviews.length"
                                readonly
                                dense
                                half-increments
                                color="yellow darken-3"
                                background-color="grey darken-1"
                                size="15"
                        ></v-rating>
                        <div>
                            <small class="warning--text  mt-1">{{ ((port.reviews.map(i=>i.rating)).reduce((a,
                                b) => a + b, 0)) / port.reviews.length ?
                                ((port.reviews.map(i=>i.rating)).reduce((a, b) => a + b, 0)) / port.reviews.length :
                                '' }}
                            </small>
                            <small class="grey--text mt-1">
                                ( {{ port.reviews.length }} )
                            </small>
                        </div>
                    </v-card-actions>
                    <!--                        <v-card-text v-html="port.description.substring(0,40) + '...'"></v-card-text>-->
                    <v-divider/>
                    <div class="pa-1">
                        <v-btn
                                block
                                :to="{ name: 'DashboardPortfolioUpdate' , params: { id: port._id } }"
                                color="primary"
                                small
                                class="ml-auto mb-1">ແກ້ໄຂ
                        </v-btn>
                        <v-btn
                                block
                                @click="removePortfolio(port._id, index)"
                                color="error"
                                small
                                class="ml-auto">ລົບ
                        </v-btn>
                    </div>
                </v-card>
            </v-col>
        </v-row>
</template>

<script>
    import { mapGetters } from 'vuex'
    import appPortfolioLoading from '../loading/dashboard/PortfolioLoading'
    export default {
        components: {
          appPortfolioLoading
        },
        data: () => ({
            portfolios: [],
            loading: false
        }),
        computed: {
            ...mapGetters([
                'auth'
            ])
        },
        methods: {
            async fetchPortfolio() {
                this.loading =true
                const userId = this.auth._id
                const results = await this.$axios.get('portfolio/getall', { params: { userId } })
                const portfolios = results.data.portfolios
                this.portfolios = portfolios
                setTimeout(() => {
                    this.loading = false
                }, 200)
            },
             async removePortfolio (id, index) {
                 const willDelete = await this.$swal({
                     title: 'ແນ່ໃຈທີ່ຈະລົບ Portfolio ແທ້ບໍ່?',
                     text: 'ລົບແລ້ວຈະບໍ່ສາມາດກູ້ຄືນໄດ້',
                     type: 'warning',
                     showCancelButton: true,
                     confirmButtonColor: '#d33',
                     cancelButtonColor: '#000',
                     confirmButtonText: 'ເຂົ້າໃຈແລ້ວ, ລົບ!!',
                     cancelButtonText: 'ບໍ່ລົບ, ກັບຄືນ'
                 })
                 if(willDelete.value) {
                     const portId = id
                     await this.$axios.delete('portfolio/delete' , { params: { _id: portId } } )
                     this.portfolios.splice(index, 1)
                     this.$swal({
                         title: 'ລົບ Portfolio ແລ້ວ',
                         type: 'success',
                         toast: true,
                         position: 'top',
                         showConfirmButton: false,
                         timer: 2000,
                     })
                     this.$io.emit('deletePortfolio', portId)
                 }
            }
        },
        created () {
            this.fetchPortfolio()
        }
    }
</script>

<style lang="scss" scoped>
    .portfolio-card {
        border: 0.5px solid whitesmoke;
        &:hover {
            transform: scale(1.015);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        }
    }
    .img-portfolio {
        border: 0.5px solid black;
        border-radius: 5px;
        width: 250px;
        height: 200px;
    }
    .title-portfolio {
        font-size: 20px;
        font-weight: bold;
    }
    .btn-category {
        text-align: end;
        margin-right: 10px;
    }
    .btn-edit {
        &:hover {
            transform: scale(1.3);
            color: deepskyblue;
        }
    }
    .btn-delete {
        transform: scale(1.1);
        &:hover {
            transform: scale(1.3);
            color: red;
        }
    }
</style>
