<template>
        <v-card outlined  class="text-center">
            <v-container fluid>
                <v-avatar width="90" height="90" class="my-3">
                    <v-img style="border-radius: 90px"
                           :src=" auth.image ? $server + auth.image : 'https://www.w3schools.com/howto/img_avatar.png'"
                    >
                        <template v-slot:placeholder>
                            <v-row
                                    class="fill-height"
                                    align="center"
                                    justify="center"
                            >
                                <v-progress-circular size="60" indeterminate
                                                     color="primary"
                                ></v-progress-circular>
                            </v-row>
                        </template>
                    </v-img>
                </v-avatar>
                <div v-if="auth.type === 'freelancer'">
                    <span style="text-decoration: underline"> ສະຖານະ:</span>
                    <v-spacer class="my-2" />
                    <v-btn @click="updateReady(true)"
                           text
                           rounded
                           :class="auth.acceptOrder === 'true' ? 'success' : ''"
                           class="font-weight-bold"
                    >ພ້ອມຮັບງານ
                    </v-btn>
                    <v-btn
                            @click="updateReady(false)"
                            text
                            rounded
                            :class="auth.acceptOrder === 'false' ? 'success' : ''"
                            class="font-weight-bold">
                        ບໍ່ພ້ອມຮັບງານ</v-btn>
                </div>
                <div class="text-left ml-5 mt-4">
                    <p>username: {{ auth.username }}</p>
                    <p>ຊື່: {{ auth.name }}</p>
                    <p>ນາມສະກຸນ: {{ auth.lastname}}</p>
                    <p>ອີເມວ: {{ auth.email }}</p>
                    <p>ເບີໂທ:  {{ auth.phone }} </p>
                </div>
                <v-divider class="my-2" />
                <v-btn :to=" { name: 'DashboardProfileEdit' } " class="primary my-2">ແກ້ໄຂໂປຣຟຣາຍ</v-btn>
            </v-container>
        </v-card>
</template>

<script>
    import { mapGetters, mapActions } from 'vuex'
    export default {
        computed: {
            ...mapGetters([
                'auth'
            ]),
        },
        methods: {
            ...mapActions([
               'fetchAuth'
            ]),
            async updateReady (con) {
               await this.$axios.put('profile/ready', {
                    _id: this.auth._id,
                    ready: con
                })
                await this.fetchAuth()
                this.$io.emit('acceptWork', this.auth._id)
            }
        }
    }
</script>

